import React, {PureComponent} from "react";
import {Button, DatePicker, Form, Input, Modal, Select, Table,message} from 'antd';
import {AllLayout} from "./style";
import {getPedestalAvailable, getRemindList, getRemindUsers,remindSave} from '../../../axios'
import moment from "moment";
import LineWrap from '../../common/lineWrap';
const {confirm} = Modal;
const {Option} = Select;
const FormItem = Form.Item;
const EditableContext = React.createContext();
import AuthComponent from "../../common/authComponent ";
const AuthButton = AuthComponent(Button);
// 编辑渲染table
class EditableCell extends React.Component {
    getInput = () => {
        let renderInput = '';
        const {dataIndex, record} = this.props;
        const disabledEndDate = (endValue) => {
            const defaultValue = record.lastBeginDate;
            const currentTime = moment();
            const startValue = currentTime;
            if (!endValue || !startValue) {
                return false;
            }
            // return endValue.valueOf() > startValue.valueOf() || endValue.valueOf() < defaultValue;
            return endValue.valueOf() < defaultValue;
        }

        switch (dataIndex) {
            case 'nextBeginDate':
                renderInput = <DatePicker format="YYYY-MM-DD" disabledDate={disabledEndDate}/>;
                break;
            case 'pedestalName':
                renderInput = <Select
                    showSearch
                    style={{width: '100%'}}
                    placeholder="请选择台座名称"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.props.children&&option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    }
                >
                    {
                        record.pedestalList && record.pedestalList.length ?
                            record.pedestalList.map(item => (
                                <Option value={item.k} key={item.v}>{item.v}</Option>
                            )) : null
                    }
                </Select>;
                break;
            case 'userName':
                renderInput = <Select
                    showSearch
                    style={{width: '100%'}}
                    placeholder="请选择负责人"
                    optionFilterProp="children"
                    filterOption={(input, option) =>
                        option.props.children&&option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                    }
                >
                    {
                        record.userList && record.userList.length ?
                            record.userList.map(item => (
                                <Option value={item.userName} key={item.realName}>{item.userName}</Option>
                            )) : null
                    }
                </Select>
                break;

            case 'remark':
                renderInput = <Input/>
                break;
        }
        return renderInput;
    };

    renderCell = ({getFieldDecorator}) => {
        const {
            editing,
            dataIndex,
            title,
            inputType,
            record,
            index,
            children,
            ...restProps
        } = this.props;
        return (
            <td {...restProps}>
                {editing ? (
                    <Form.Item style={{margin: 0}}>
                        {getFieldDecorator(`${dataIndex}-${record.id}`, {
                            rules: [
                                {
                                    required: dataIndex!='remark',
                                    message: `请输入${title}!`,
                                },
                            ],
                            initialValue: '',
                        })(this.getInput())}
                    </Form.Item>
                ) : (
                    children
                )}
            </td>
        );
    };

    render() {
        return <EditableContext.Consumer>{this.renderCell}</EditableContext.Consumer>;
    }
}

class ProcessRemind extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            remindList: [],
            selectedRowKeys: [],
            // 筛选类型
            type: 1,
            // 编辑更新状态
            isEdit: false,
            // 更新参数
            remindParams: [],
            loading: false
        };
        this.columns = [
            {
                title: '序号',
                key: 'id',
                width: 50,
                render: (text, record, index) => {
                    return record.warn == 1 ?
                        index + 1 :
                        <span className="text-color">{index + 1}</span>
                }
            },
            {
                title: '桥梁名称',
                dataIndex: 'bridgeName',
                width: 100,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />
                /* render:(text)=>{
                    console.log(text,'text')
                } */
            },
            {
                title: '桩号范围',
                dataIndex: 'mileage',
                width: 100,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
            },
            {
                title: '梁体类型',
                dataIndex: 'typeName',
                width: 100,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
            },
            {
                title: '梁体名称',
                dataIndex: 'name',
                width: 100,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
            },
            {
                title: '下一步状态',
                dataIndex: 'nextStateName',
                width: 100,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
            },
            {
                title: '开始时间',
                dataIndex: 'nextBeginDate',
                width: 150,
                render: (text, record) => record.warn == 1 ? moment(text).format('YYYY-MM-DD') :
                    <span className="text-color">{moment(text).format('YYYY-MM-DD')}</span>,
                editable: true
            },
            {
                title: '台座类型',
                dataIndex: 'nextStageName',
                width: 100,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
            },
            {
                title: '台座名称',
                dataIndex: 'pedestalName',
                width: 150,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
                editable: true
            },
            {
                title: '负责人',
                dataIndex: 'userName',
                width: 150,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} /> ,
                editable: true

            },
            {
                title: '备注',
                dataIndex: 'remark',
                width: 150,
                render: (text, record) => record.warn == 1 ? text : <LineWrap  title={text} />,
                editable: true
            }
        ];
    }

    // 下拉筛选
    handleChange = (value) => {
        this.setState({
            type: value,
        }, () => {
            this.getRemindList()
        });
    }

    // 更新提交
    handleBatchSubmit() {
        let isError = false;
        this.props.form.validateFields((err, fieldsValue) => {
            // debugger;
            if(JSON.stringify(fieldsValue) == "{}") { isError = true }

        })
        if (isError) {
            message.error("不能编辑");
            return false;
        }
        confirm({
            title: '将批量更新信息填写完整的梁体状态，保存提交后将无法再次修改，是否确认提交？',
            okText: '取消',
            cancelText: '确定',
            onOk:()=> {
                console.log('OK');
            },
            onCancel:()=> {
                this.props.form.validateFields((err, fieldsValue) => {
                    if(err) return
                    const params=[];
                    const hash = {};//数组对象去重使用
                    for (let fieldsValueProp in fieldsValue) {
                        const Id=fieldsValueProp.split('-')[1];
                        const Prop=fieldsValueProp.split('-')[0];
                        const curEditRow=this.state.remindList.find(item=>item.id==Id);
                        if(curEditRow){
                            curEditRow[Prop]=fieldsValue[fieldsValueProp]
                            if(Prop=='nextBeginDate'){
                                curEditRow[Prop]=moment(fieldsValue[fieldsValueProp]).format('YYYY-MM-DD')
                            }
                            // console.log(fieldsValue[fieldsValueProp]);
                            if(Prop=='pedestalName'){
                                curEditRow['pedestalId']=fieldsValue[fieldsValueProp]
                            }
                            params.push({
                                stateId: curEditRow.nextStateId,
                                bodyId: curEditRow.id,
                                pedestalId:curEditRow.pedestalId,
                                beginDate: curEditRow.nextBeginDate,
                                remark: curEditRow.remark,
                                userName:curEditRow.userName
                            })
                        }
                      }
                    //去重
                    const remindParams = params.reduceRight((item, next) => {
                        hash[next.bodyId] ? '' : hash[next.bodyId] = true && item.push(next);
                        return item
                    }, []);
                    // console.log(params,'params');
                    // console.log(remindParams,'remindParams');
                    // this.getRemindList()
                    remindSave(remindParams).then(res => {
                        if (res.code == 1) {
                            message.success('更新成功');
                            this.getRemindList()
                        }else {
                            message.warning(res.msg);
                            this.getRemindList()
                        }
                    })
                });
            },
        });
    }

    // 拖动列宽
    handleResize = index => (e, {size}) => {
        this.setState(({columns}) => {
            const nextColumns = [...columns];
            nextColumns[index] = {
                ...nextColumns[index],
                width: size.width,
            };
            return {columns: nextColumns};
        });
    };

    // 多选
    onSelectChange = (selectedRowKeys, selectedRows, remindList) => {
        const authcodes = JSON.parse(sessionStorage.getItem("authCode"));
        this.setState({
            selectedRowKeys,
            isEdit:!authcodes.funAuthCode.includes("77006002")?false:true
        });
        remindList.forEach(item => {
            item.isEdit = false
        })
        selectedRows.forEach(item => {
            item.isEdit = item.nextStateId?true:false;
        })
    };

    render() {
        let columnsDefault = this.columns
        this.state.remindList = this.state.remindList.filter(item=>item!=undefined);
        this.setState(this.state.remindList)
        const {selectedRowKeys, remindList, isEdit, loading} = this.state;
        const components = {
            body: {
                cell: EditableCell,
            },
        };

        const rowSelection = {
            selectedRowKeys,
            onChange: (selectedRowKeys, selectedRows) => this.onSelectChange(selectedRowKeys, selectedRows, remindList),
            hideDefaultSelections: true,
            // 禁止勾选
            // getCheckboxProps: record => ({
            //     disabled: record.warn === 2,
            // }),
        };

        if (remindList && remindList.length) {
            // 出场阶段不展示台座
            if (remindList.every(item => item.status == 2)) {
                columnsDefault = this.columns.filter(item => (item.dataIndex != 'nextStageName' && item.dataIndex != 'pedestalName'))
            }
        }

        const columns = columnsDefault.map((col, index) => {
            if (!col.editable) {
                return col;
            }
            return {
                ...col,
                onCell: record => ({
                    record,
                    dataIndex: col.dataIndex,
                    title: col.title,
                    editing: record.isEdit,
                })
            };
        });
        const authcodes = JSON.parse(sessionStorage.getItem("authCode"));
        return (
            <AllLayout>
                <div className='layout-header'>
                    <AuthButton type="primary" source="fun" authcode="77006002" authcodes={authcodes.funAuthCode} disabled={!isEdit}   onClick={() => {
                            this.handleBatchSubmit()
                        }}>更新提交
                    </AuthButton>

                    <Form
                        layout="inline"
                        style={{
                            float: 'right',
                            marginTop: '-4px'
                        }}>
                        <FormItem label="">
                            <Select
                                onChange={this.handleChange}
                                placeholder="全部"
                                style={{
                                    width: 200
                                }}>
                                <Option value="1">全部</Option>
                                <Option value="2">待生产</Option>
                                <Option value="3">已延期(红色)</Option>
                            </Select>
                        </FormItem>
                    </Form>
                </div>
                <EditableContext.Provider value={this.props.form}>
                    <Table
                        loading={loading}
                        rowSelection={rowSelection}
                        rowKey="id"
                        components={components}
                        bordered
                        dataSource={remindList}
                        columns={columns}
                        // rowClassName="editable-row"
                        pagination={false}
                        scroll={{y: 600}}
                        className="tableStyle"
                    />
                </EditableContext.Provider>
            </AllLayout>
        )
    }

    componentWillMount() {
        this.getRemindList()
    }

    getRemindList() {
        const {type} = this.state;
        this.setState({
            loading: true
        });
        // 查询类别(1：全部；2：待生产；3：已延期)
        getRemindList(type).then(async (res) => {
            let remindList = [];
            remindList = res.data.data.items;
            if (!remindList || !remindList.length) return this.setState({
                loading: false
            });

            const dataList = await Promise.all(remindList.map(async (item) => {
                if(!item.nextStateId) return item;
                // 等待异步操作完成，返回执行结果
                const {data: {data: {items: userList}}} = await getRemindUsers(item.nextStateId);
                if (!userList) return;
                // 等待异步操作完成，返回执行结果
                const {data: {data: {items: pedestalList}}} = await getPedestalAvailable(item.nextStage,item.id);
                if (!pedestalList) return;
                return {
                    ...item,
                    userList: userList,
                    pedestalList: pedestalList,
                }
            }))
            console.log(dataList,'dataList')
            this.setState({
                selectedRowKeys:[],
                remindList: dataList,
                loading: false
            });

        })
    }
}

export default Form.create()(ProcessRemind)